body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* ====================================
  Project Specific Stylesheet
===================================== */
.debug1 { background-color:rgba(255, 140, 140, 0.2); }
.debug2 { background-color:rgba(140, 255, 140, 0.2); }

body {
  overflow-x:hidden;
}

#root, html, body, .container-fluid, .splash-screen {
  height:100%;
}

.container-fluid {
  padding-left:0px !important;
  padding-right:0px !important;
}

.splash-screen {
  background-image:url(../images/scribbliator-home-3.jpg);
  background-repeat:no-repeat;
  background-attachment:scroll;
  background-position: center;
  background-size:cover;
}

.text-center-align {
  text-align:center;
}

.perfect-center {
  text-align:center;

  height:500px;
  position:absolute;
  left:0px; right:0px; top:0px; bottom:0px;
  margin:auto;
}

h1.appTitle {
  font-family:'Girassol', cursive;
  font-size:5rem;
  text-shadow:0px 3px 6px white;
  letter-spacing:5px;
  margin-bottom:0.5em;
}

p.slogan {
  padding:20px;
  text-align:center;
  text-shadow:1px 1px 3px white;
}

@media (min-width: 576px) {
  p.slogan {
    font-size:3.4vw;
  }
}

@media (min-width: 768px) {
  p.slogan {
    font-size:3.1vw;
  }
}

@media (min-width: 992px) {
  p.slogan {
    font-size:2.8vw;
  }
}

@media (min-width: 1200px) {
  p.slogan {
    font-size:22px;
  }
}
